import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from '../../provider/apiservices.service';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';

@Component({
  selector: 'app-redeem-home',
  templateUrl: './redeem-home.page.html',
  styleUrls: ['./redeem-home.page.scss'],
})

export class RedeemHomePage implements OnInit{

  redeemdata:any;
  allData:any;
  settingData:any;

  constructor(private router:Router,private storageService:LocalStorageService,private apiservices:ApiservicesService,
    private sendData:DataTransferService,private zone:NgZone) { }
  
  
  
  
  
  
  
  
  
  ngOnInit(){
    this.storageService.getStorage().then(res=>{
      this.redeemHomeDetail();
      console.log("ionic lifecycle called getStorage==>",res);
    });
  }

  redeemHomeDetail(){
    this.zone.run(()=>{
      console.log("redeem home page function==");
      let apiKey={};
      console.log("redeem home page api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.redeemFirstPage, apiKey).subscribe((result) => {
        console.log("redeem home page Api Result==", result);
        if (result.status == 1 || result.status == "1") {
          this.redeemdata=result;
          this.allData=result.data;
          this.settingData=result.data.settings;
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      }, (err)=>{
        console.log("last array of account statement==", err);
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    })
  }

  information(){
    this.zone.run(()=>{
      let termData={
        "page_title":this.allData.settings.tnc_header,
        "discription":this.allData.tnc
      }
      this.sendData.myParam=termData;
      this.router.navigate(["/redeem-information"]);
    });
  }  

  redeemNow(){
    this.zone.run(()=>{
      this.router.navigate(["/redeem-voucher-category-list"]);
    });
  }

  statementOrder(clickRedeemData,redirectionValue){
    this.zone.run(()=>{
      this.sendData.myParam=clickRedeemData;
      console.log("click value==", redirectionValue);
      if(redirectionValue=="statement"){
        this.router.navigate(["/accountstatement"]);
      }else{
        this.router.navigate(["/redeem-my-order-list"]);
      }
    });
  }









}